// SB Link Variables:
// --sb-link__color - link text color
// --sb-link__color_hover - link text hover color
// --sb-link__size - link font size
// --sb-link__weight - link font weight

.sb-link {
  color: var(--sb-link__color, var(--sbt-color-body-link));
  font-size: var(--sb-link__size, var(--sbt-font-size-30));
  font-weight: var(--sb-link__weight, var(--sbt-font-weight-40));

  &:hover {
    color: var(--sb-link__color_hover, var(--sbt-color-body-link-dark));
  }

  &.sb-link_underline_none {
    text-decoration: none;
  }

  &.sb-link_underline_hover {
    // Remove link default text-decoration to only show underline on hover.
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  &.sb-link_underline_always {
    text-decoration: underline;
  }

  &.sb-link_disabled {
    color: var(--sbt-color-disabled-text);
    cursor: not-allowed;

    &:hover {
      color: var(--sbt-color-disabled-text);
    }
  }
}
